@import url('https://fonts.googleapis.com/css2?family=Parisienne&family=Playfair+Display:ital,wght@0,400..900;1,400..900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Tangerine:wght@400;700&display=swap');

body, #root {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
}

html {
  scroll-behavior: smooth;
}

.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* HEADER FIXO */
.Header {
  position: fixed;
  top: 0;
  z-index: 10;
  background-color: #1f1a1a;
  height: 14vh;
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding: 0 2%;
  box-sizing: border-box;
  color: #fff;
}

/* TITULO */
.Hsw p {
  font-family: "Tangerine", cursive;
  font-weight: 400;
  font-style: italic;
  font-size: 1.5rem;
  margin: 0;
  white-space: nowrap;
}

/* NAVIGATION */
.Navigation {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex: 1;
}

.Navigation p {
  font-family: "Playfair Display", sans-serif;
  font-weight: 400;
  font-style: italic;
  font-size: 1.2rem;
  color: rgb(197, 197, 197);
  cursor: pointer;
  margin: 0 0.5rem;
  transition: color 0.3s;
}

.Navigation p:hover {
  color: #ffffff;
}

/* LOGO */

.Logo {
  margin: 0;
  white-space: nowrap;
}
.Logo img {
  width: auto;
  height: 40%;
}

/* BODY */
.Body {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

/* SEÇÕES */
.Home, .Vantagens, .Contato {
  width: 100%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.Home { background-color: #ec0505; }
.Vantagens { background-color: #ff8c00; }
.Contato { background-color: #09ff00; }

/* FOOTER */
.Footer {
  background-color: #282c34;
  width: 100%;
  padding: 2rem 0;
  font-size: 1.5rem;
  color: #fff;
  display: flex;
  justify-content: center;
}

/* MEDIA QUERIES - RESPONSIVO */
@media (max-width: 768px) {
  .Header {
    flex-direction: column;
    height: auto;
    padding: 1rem;
  }

  .Hsw p {
    font-size: 2rem;
  }

  .Navigation {
    width: 100%;
    flex-wrap: wrap;
    margin-top: 0.5rem;
  }

  .Navigation p {
    font-size: 1rem;
    margin: 0.3rem;
  }

  .Logo img {
    height: 60px;
    margin-top: 0.5rem;
  }

  .Footer {
    font-size: 1rem;
    padding: 1rem 0;
  }
}
